+ 想要追求更好膚質者</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/laserBeauty/2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/laserBeauty/2.jpg")))+" alt=\"肌膚問題\"></picture></div> <div class=\"article-container\"><h2>美麗案例</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/laserBeauty/案例1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/laserBeauty/案例1.png")))+" alt=\"案例1\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>- 雷射治療後，皮膚會有輕微紅熱感，如有不適可進行冰敷。</li> <li>- 術後可搭配保濕面膜或保濕導入課程。</li> <li>- 部分患者接受治療後會有痘痘或搔癢之情形發生，此為皮膚代謝之正常現象，3~5天會自然消退。</li> <li>- 療程後2天內避免使用含有果酸或酒精成分的保養品或化妝水，且一週內請勿進行其它護膚及去角質療程。</li> <li>- 淨膚雷射無開放性傷口，術後肌膚無不適即可正常洗臉化妝。 居家照護請加強保濕及防曬，減少戶外活動或避免直接曝曬陽光，白天請使用防曬係數SPF30或以上防曬品。</li></ul></div>")],2)}
